.phone {
  @include link($primaryColor);
  @include hover(color, $hoverColor);

  &--mobile {
    display: none;
    padding: 3px;
    height: 40px;
    width: 40px;
    background-image: url(../images/phone_icon.svg);
    background-repeat: no-repeat;
    background-size: cover;
  }
}

@media (max-width: $smallScreenBreakPoint) {
  .phone {
    display: none;

    &--mobile {
      display: block;
    }
  }
}
