.footer {
  display: flex;
  height: 360px;
  background-image: url(../images/footer.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__logo {
    align-self: flex-end;
    margin: 0 auto 40px;
  }
}

@media (max-width: $mobileScreeBreakPoint) {
  .footer__logo {
    height: 50px;
    width: 77px;
  }
}
